.cs__possibility {
    display: flex;
    flex-direction: row;
    padding: 20px;
  }
  
  .cs__possibility-image {
    margin: 0 1.5rem;
    flex: 1;
  }
  
  .cs__possibility-image img {
    width: 100%;
    height: 100%;
  }
  
  .cs__possibility-container {
    flex: 1.5;
    font-family: var(--font-family);
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    margin: auto 1rem;
  }
  
  .cs__possibility-container h1 {
    font-weight: 800;
    font-size: 33px;
    line-height: 45px;
    margin: 1rem 0;
    background: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  
  .cs__possibility-container p.access-link {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    cursor: pointer;
  }
  
  .cs__possibility-container p.cs__possibility-description {
    color: var(--color-text);
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
  }
  
  @media screen and (max-width: 960px) {
    .cs__possibility {
      flex-direction: column;
    }
  
    .cs__possibility-container {
      margin: 2rem;
    }
  
    .cs__possibility-container h1 {
      font-size: 25px;
      line-height: 30px;
    }
  }
  